.carousel {
  margin-top: 5.375rem;
  @media (min-width: $screen-md) and (max-width: $screen-md-max) {
    margin-top: 3.125rem;
  }
  @media (max-width: $screen-sm-max) {
    margin-top: 0;
  }

  .carousel-inner {
    position: relative;
    box-shadow: 0 0px 13px 0 rgba(0, 0, 0, 0.15);
    @media (max-width: $screen-sm-max) {
      box-shadow: none;
    }
  }
}

.item {
  display: none;
  max-width: 1120px;
  height: 450px;
  @media (max-width: $screen-sm) {
    height: 273px;
    background-color: $catskillWhite-color;
  }
  overflow: hidden;

  > img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    @media (max-width: $screen-sm) {
      margin-top: 0;
      height: 204px;
    }
    //@media (max-width: 380px){
    //  object-position: -150px 0px;
    //}
  }

  &.active {
    display: block;
  }
}

.carousel-control {
  display: block;
  position: absolute;
  top: 50%;
  @media (max-width: $screen-sm) {
    top: 90px;
  }
  height: 15px;
  width: 15px;
  border: solid $silver-color;
  border-width: 0 3px 3px 0;
  margin: 10px;
  opacity: 0.4;
  cursor: pointer;
  transition: opacity .2s ease-in-out;

  &.left {
    left: 0;
    transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
  }

  &.right {
    right: 0;
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
  }
}

.left:hover,
.right:hover {
  opacity: 0.8;
}

.carousel-indicators {
  @media (max-width: $screen-sm) {
    display: none;
  }
  position: absolute;
  bottom: 23px;
  right: 17px;
  z-index: 15;

  li {
    cursor: pointer;
    width: 8px;
    height: 8px;
    border-radius: 100%;
    background-color: $white-color;
    margin-left: 10px;

    &.active {
      background-color: $emperor-color;
    }
  }
}

.carousel-caption {
  > img {
    position: absolute;
    top: 50px;
    left: 92px;
    @media (max-width: $screen-sm) {
      display: none;
    }
  }

  > h3 {
    position: absolute;
    top: 190px;
    left: 92px;
    color: $tamarillo-color;
    font-size: 1.5rem;
    font-family: "PlayfairDisplay", sans-serif;
    font-weight: 400;
    @media (max-width: $screen-sm) {
      display: none;
    }
  }

  > p {
    position: absolute;
    top: 235px;
    left: 92px;
    color: $black-color;
    font-size: 1rem;
    font-family: "JosefinSans", sans-serif;
    font-weight: 300;
    max-width: 329px;
    overflow: hidden;
    @media (max-width: $screen-sm) {
      display: none;
    }
  }

  .wave-btn {
    position: absolute;
    bottom: 57px;
    left: 92px;
    border: 1px solid $silver-color;
    width: 100px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0 0 5px 5px;
    text-transform: uppercase;
    font-size: 0.75rem;
    font-family: "JosefinSans", sans-serif;
    font-weight: 700;
    transition: all .8s ease 0s;
    overflow: hidden;

    .wave-btn__text {
      position: relative;
      z-index: 1;
      color: $silver-color;
      text-shadow: 0 50px rgba(0, 0, 0, 0.5);
    }

    .wave-btn__waves {
      position: absolute;
      width: 100px;
      height: 100px;
      top: 0;
      left: 0;
      background-color: $tamarillo-color;
      transition: all 2s ease 0s;

      &:before, &:after {
        content: "";
        position: absolute;
        top: 0;
        left: 50%;
        width: 250%;
        height: 250%;
        transform: translate3d(-50%, -96%, 0) rotate(0deg) scale(1);
      }

      &:before {
        background-color: rgba(248, 93, 115, 0.8);
        border-radius: 45%;
        animation: waves 5s infinite linear;
      }

      &:after {
        background-color: $alabaster-color;
        border-radius: 44%;
        animation: waves 10s infinite linear;
      }

      @keyframes waves {
        0% {
          transform: translate3d(-50%, -96%, 0) rotate(0deg) scale(1);
        }
        100% {
          transform: translate3d(-50%, -96%, 0) rotate(360deg) scale(1);
        }
      }

    }

    &:hover {
      border-radius: 5px;
      color: $white-color;
      transition: all .5s ease 0s;
      box-shadow: 0 0 40px rgba(145, 16, 38, .5);
      text-shadow: none;

      .wave-btn__waves {
        top: -50px;
      }
    }

    @media (max-width: $screen-sm) {
      border: none;
      top: auto;
      bottom: 15px;
      font-size: 18px;
      left: auto;
      right: auto;
      width: 100%;
      text-align: center;
      :before, :after {
        display: none;
      }
      .wave-btn__waves {
        display: none;
      }
      &:hover {
        box-shadow: none;
        border-radius: 0;
      }
    }
  }
}

