.popular {
  margin-top: 5.375rem;
  @media (min-width: $screen-md) and (max-width: $screen-md-max) {
    margin-top: 3.125rem;
  }
  @media (max-width: $screen-sm-max) {
    margin-top: 25px;
  }

  ::-webkit-scrollbar {
    width: 2px;
  }

  ::-webkit-scrollbar-track {
    background-color: $Alto-color;
  }

  ::-webkit-scrollbar-thumb {
    background: $emperor-color;
  }

  .popular__title {
    font-size: 2rem;
    font-family: PlayfairDisplay, sans-serif;
    font-weight: 900;

    &.tomarillo {
      color: $tamarillo-color;
      margin-bottom: 2rem;
      text-align: center;

      &:after {
        content: '';
        display: block;
        width: 40%;
        min-width: 320px;
        height: 2px;
        margin: 0.625rem auto 0;
        background-color: $white-color;
      }
    }

    &.green {
      color: $deepSeaGreen-color;
      @media (max-width: $screen-sm) {
        font-size: 1.85rem;
      }
    }
  }


  .popular__container {
    background-color: $white-color;
    box-shadow: 0 0px 13px 0 rgba(0, 0, 0, 0.15);
    //height: 440px;
    @media (max-width: $screen-sm) {
      box-shadow: none;
    }

    .popular__row {
      @media (max-width: $screen-sm) {
        flex-direction: column;
      }
    }
  }

  .popular__body {
    flex-direction: column;
    padding: 32px 50px 4px 36px;
    @media (max-width: $screen-sm) {
      padding: 32px 20px 17px 36px;
    }
  }

  .popular__column-1 {
    flex-basis: 520px;
    font-size: 0;
    @media (max-width: $screen-sm) {
      display: none;
    }

    img {
      width: 100%;
      height: 440px;
      object-fit: cover;
    }
  }

  .popular__column-2 {
    flex-basis: 600px;
    @media (max-width: $screen-sm) {
      flex-basis: auto;
      background-image: url("../img/popular-1.png");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      text-shadow: 1px 1px 2px black, 0 0 1em $deepSeaGreen-color;
    }
  }

  .popular__time {
    margin-top: 20px;
    align-items: flex-end;
  }

  .cards__clock {
    font-size: 1rem;
    margin-right: 11px;
    align-items: flex-end;
  }
  .cards__clock::before {
    width: 15px;
    height: 15px;
  }
  .cards__clock::after {
    display: none;
  }

  .popular__complexity {
    -ms-flex-align: center;
    align-items: flex-end;
    font-size: 1.125rem;
    overflow: hidden;
    white-space: nowrap;
    color: $doveGray-color;
    font-family: "JosefinSans", sans-serif;
    font-weight: 300;
    span {
      margin-bottom: -3px;
    }
  }

  .popular__complexity:before {
    background: url("../img/easy-icon.png") no-repeat center/contain;
    content: '';
    display: block;
    width: 26px;
    height: 24px;
    margin-right: 11px;
  }
}

.popular__btn {
  margin-bottom: -7px;
  position: relative;
  margin-left: auto;
  font-size: 0.75rem;
  text-transform: uppercase;
  border: 1px solid $silver-color;
  padding: 9px 15px;
  font-family: "JosefinSans", sans-serif;
  font-weight: 300;
  width: 110px;
  height: 30px;
  text-align: center;
  display: block;
  overflow: hidden;
  transition: .2s ease-in-out;

  &:before {
    content: "";
    background: linear-gradient(90deg, rgba(255, 255, 255, .1), rgba(255, 255, 255, .5));
    height: 50px;
    width: 50px;
    position: absolute;
    top: -8px;
    left: -75px;
    transform: skewX(-45deg);
  }

  &:hover {
    background: $deepSeaGreen-color;
    color: #fff;
  }

  &:hover:before {
    left: 150px;
    transition: .5s ease-in-out;
  }
}

.popular__filling {
  max-height: 265px;
  overflow: auto;
  margin-top: 25px;

  .popular__subdescr {
    max-width: 480px;
    margin-bottom: 22px;
    font-size: 1rem;
    line-height: 19px;
    font-family: "JosefinSans", sans-serif;
    font-weight: 300;
  }

  .popular__subtitle {
    font-size: 1rem;
    font-family: "PlayfairDisplay", sans-serif;
    font-weight: 700;
  }

  .popular__descr {
    max-width: 495px;
    margin: 25px 0 0 0;
    font-size: 1rem;
    line-height: 19px;
    font-family: "JosefinSans", sans-serif;
    font-weight: 300;

    span {
      text-transform: uppercase;
    }
  }
}
