.footer {
  margin-top: 5.125rem;
  @media (min-width: $screen-md) and (max-width: $screen-md-max) {
    margin-top: 3.125rem;
  }
  @media (max-width: $screen-sm-max) {
    margin-top: 25px;
  }

  .footer__top {
    align-items: center;

    @media (max-width: $screen-md) {
      align-items: flex-start;
      justify-content: space-between;
      //border-bottom: 2px solid $dustyGray-color;
    }
  }

  .footer__list {
    margin-right: 100px;
    @media (min-width: $screen-md) and (max-width: $screen-md-max) {
      margin-right: auto;
      font-size: 0.625rem;
    }
    justify-content: space-between;
    @media (max-width: $screen-md) {
      margin: 0;
      flex-direction: column;
      width: 150px;
    }

    > li {
      margin-left: 10px;
    }

    .footer__link {
      display: block;
      text-transform: uppercase;
      color: $emperor-color;
      padding: 10px;
      font-size: 0.75rem;
      width: 100%;
      text-align: right;
      @media (min-width: $screen-md) and (max-width: $screen-md-max) {
        font-size: 0.625rem;
      }

      font-family: "JosefinSans", sans-serif;
      font-weight: 700;
    }
  }

  .footer__container {
    background-color: $mineShaft-color;
    padding: 35px 35px 35px 87px;
    @media (max-width: $screen-md) {
      padding: 35px 35px 40px 30px;
    }
    @media (min-width: $screen-md) and (max-width: $screen-md-max) {
      padding: 15px;
    }
  }

  .footer__info {
    text-transform: uppercase;
    color: $emperor-color;
    font-size: 0.75rem;
    font-family: "JosefinSans", sans-serif;
    font-weight: 700;
    text-align: right;
    padding: 10px 0;

    @media (max-width: $screen-md) {
      max-width: 150px;
      margin-top: 10px;
      line-height: 1.5;
    }
  }

  .footer-right {
    margin-left: auto;
    @media (max-width: $screen-md) {
      flex-direction: column;
      align-items: flex-end;
    }
  }

  .footer__bottom {
    width: 100%;
    position: relative;
    @media (max-width: $screen-md) {
      &:after {
        content: "";
        display: block;
        width: 100%;
        height: 2px;
        border-top: 2px solid $dustyGray-color;
        position: absolute;
        right: 0;
        bottom: 60px;
      }
    }

    @media (min-width: $screen-md) {
      justify-content: flex-end;
    }

    .footer__links {
      @media (max-width: $screen-md) {
        margin-top: -175px;
        width: 100px;
      }
      width: 100px;
      justify-content: space-between;

      .footer__links-item {
        color: $emperor-color;
      }
    }
  }
}
